import React, { useState, useEffect } from 'react'
import { Bar } from 'react-chartjs-2';
import BarGraf from './BarGraf';

function GrafyKrajina({ match }) {
    // console.log(match.params.id)
    // const [dataKrajina, setDataKrajina] = useState()

    const [dataforChart, setDataforChart] = useState()




    useEffect(() => {
        const fetchData = async () => {
            const response1 = await fetch(`https://disease.sh/v2/historical/${match.params.id}`)
            let data1 = await response1.json()

            let datumy = Object.keys(data1.timeline.cases)
            datumy.map((jedenDatum, id) => {
                var d = new Date(jedenDatum);
                var datestring = d.getDate() + ". " + (d.getMonth() + 1) + ". " + d.getFullYear()
                datumy[id] = datestring
            })

            let valuesCases = Object.values(data1.timeline.cases)

            let valueRecovered = Object.values(data1.timeline.recovered)

            let valueDeaths = Object.values(data1.timeline.deaths)
            let valueActiveCases = []

            valuesCases.map((oneCase, id) => {
                valueActiveCases[id] = oneCase - valueRecovered[id] - valueDeaths[id]
            })

            setDataforChart(
                {
                    datumy: datumy,

                    dataSetsCases: {
                        datasets: [
                            {
                                label: "Celkový počet nakazených",
                                data: valuesCases,
                                backgroundColor: "rgb(249, 70, 68)",
                                fillColor: 'black',
                                hoverBackgroundColor: 'red',
                            }
                        ]
                    },

                    dataSetsDeaths: {
                        datasets: [
                            {
                                label: "Celkový počet úmrtí",
                                data: valueDeaths,
                                backgroundColor: "grey",
                                hoverBackgroundColor: 'grey',
                            }
                        ]
                    },

                    dataSetsRecovered: {
                        datasets: [
                            {
                                label: "Celkový počet vyliečených",
                                data: valueRecovered,
                                backgroundColor: "green",
                                hoverBackgroundColor: 'green',
                            }
                        ]
                    },

                    dataSetsActiveCases: {
                        datasets: [
                            {
                                label: "Celkový počet aktivnych prípadov",
                                data: valueActiveCases,
                                backgroundColor: "blue",
                                hoverBackgroundColor: 'blue',
                            }
                        ]
                    },

                }
            )

        }
        fetchData()
    }, [])

    return (
        dataforChart ?
            <div className="testingGrafy">
                <h1>Covid-19 v krajine {match.params.id}</h1>

                <div >
                    <BarGraf props={dataforChart} />
                    {/* <Bar
                        data={dataforChart}

                        options={{
                            legend: {
                                position: "bottom",
                                labels: {
                                    padding: 20,
                                }
                            },
                            scales: {
                                xAxes: [{
                                    gridLines: {
                                        drawOnChartArea: false
                                    }
                                }],
                                yAxes: [{
                                    ticks: {
                                        callback: function (value, index, values) {
                                            return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")
                                        }
                                    }
                                }]
                            },
                            tooltips: {
                                callbacks: {
                                    label: function (tooltipItem, chart) {
                                        var datasetLabel = chart.datasets[tooltipItem.datasetIndex].label || '';

                                        return datasetLabel + ": " + tooltipItem.yLabel.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")
                                    }
                                },
                            }
                        }}
                    /> */}
                </div>
            </div>
            : null
    )
}

export default GrafyKrajina
